import { Mark } from '@ali/4ever-cangjie';

type CapsMark = Mark;

export interface CapData {
  value: boolean;
}

const Caps = {
  TYPE: 'caps',

  create(capData?: CapData | null): CapsMark {
    // 这里capData可能传入null是因为在线编辑场景下服务端转换的数据可能为null
    // 所以这里需要兼容一下这种情况
    const cap = capData ? capData.value : false;
    return Mark.create({ type: Caps.TYPE,
      data: {
        value: !!cap,
      } });
  },

  isCaps(maybeCaps: any): maybeCaps is CapsMark {
    return Mark.isMark(maybeCaps) && maybeCaps.type === Caps.TYPE;
  },
};

export default Caps;
